import { CancelToken } from "axios";
import request from "./request";

export const get = id => request({
    url: `/${id}`,
    method: "GET",
    cancelToken: CancelToken.source().token
});

export const post = content => request({
    url: "/post/",
    method: "POST",
    cancelToken: CancelToken.source().token,
    data: {
        content
    }
});
