import React, { Component } from "react";
import { Editor } from "react-draft-wysiwyg";
import { EditorState } from "draft-js";
import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import styles from "./SettingModal.module.css";
import { formValidation, submitValidation, errorClass } from "../../../utils/form-validation";
import Modal from "../../../components/Modal";
import Button from "../../../components/Common/Button";
import Form from "../../../components/Common/Form";
import Input from "../../../components/Common/Input";
import Select from "../../../components/Common/Select";

class SettingModal extends Component {
    state = {
        imageUrlInput: [""],
        form: {
            title: "",
            category: "Illustrations",
            imageUrl: [""],
            desc: EditorState.createEmpty()
        },
        formErrors: {
            title: "",
            category: "",
            imageUrl: [""]
        },
        formValidity: {
            title: false,
            category: false,
            imageUrl: [false]
        }
    };

    handleFormSubmit = e => {
        e.preventDefault();
    };

    handleSubmitValidation = () => {
        const { formValidity, form } = this.state;
        this.setState({ canSubmit: submitValidation(form, formValidity) });
    };

    handleInputChange = (e, index = 0) => {
        e.preventDefault();
        const { value, name } = e.target;
        const { formErrors, formValidity, form } = this.state;
        const { validationError, validity } = formValidation(
            name,
            value,
            formValidity,
            formErrors,
            index
        );
        const imgUrlValues = [...form.imageUrl];

        if (name === "imageUrl") {
            imgUrlValues[index] = value;
        }

        this.setState(
            prevState => ({
                form: {
                    ...prevState.form,
                    [name]: name === "imageUrl" ? imgUrlValues : value
                },
                formErrors: validationError,
                formValidity: validity
            }),
            () => {
                this.handleSubmitValidation();
            }
        );
    };

    handleDescInputChange = descState => {
        this.setState(
            prevState => ({
                form: {
                    ...prevState.form,
                    desc: descState
                }
            }),
            () => {
                this.handleSubmitValidation();
            }
        );
    };

    handleAddimageUrlInput = () => {
        this.setState(
            prevState => ({
                formValidity: {
                    imageUrl: [...prevState.formValidity.imageUrl, false]
                },
                imageUrlInput: [...prevState.imageUrlInput]
            }),
            () => this.handleSubmitValidation()
        );
    };

    handleRemoveimageUrlClick = index => {
        const { imageUrlInput, form, formValidity, formErrors } = this.state;

        const values = [...imageUrlInput];
        const inputValues = [...form.imageUrl];
        const inputValidity = [...formValidity.imageUrl];
        const inputErros = [...formErrors.imageUrl];

        values.splice(index, 1);
        inputValues.splice(index, 1);
        inputValidity.splice(index, 1);
        inputErros.splice(index, 1);
        this.setState(
            {
                imageUrlInput: values,
                form: {
                    imageUrl: inputValues
                },
                formValidity: {
                    imageUrl: inputValidity
                },
                formErrors: {
                    imageUrl: inputErros
                }
            },
            () => this.handleSubmitValidation()
        );
    };

    renderImageUrlInput = () => {
        const { form, formErrors, imageUrlInput } = this.state;
        const { imageUrl } = form;

        return imageUrlInput.map((el, index) => (
            <div className={styles.settings_modal_imageurl_wrapper} key={index}>
                <Input
                    type="text"
                    name="imageUrl"
                    id="imageUrl"
                    value={imageUrl[index] || ""}
                    onChange={e => this.handleInputChange(e, index)}
                    className={errorClass(formErrors.imageUrl[index])}
                    errorMsg={formErrors.imageUrl[index]}
                    placeholder="Your design image url"
                />

                {index > 0 && (
                    <div
                        className={styles.settings_modal_remove_imageurl_icon}
                        onClick={e => this.handleRemoveimageUrlClick(index)}
                        role="presentation"
                    >
                        <FontAwesomeIcon icon="times" size="1x" />
                    </div>
                )}

                {index === 0 && (
                    <div
                        className={styles.settings_modal_add_imageurl_icon}
                        onClick={this.handleAddimageUrlInput}
                        role="presentation"
                    >
                        <FontAwesomeIcon icon="plus" size="1x" />
                    </div>
                )}
            </div>
        ));
    };

    render() {
        const { form, formErrors, canSubmit } = this.state;
        const { isModalShow, isModalEditShow, onCloseModal } = this.props;
        const { title, category } = form;
        const categories = [
            {
                name: "Illustrations",
                value: "Illustrations"
            },
            {
                name: "Logo",
                value: "logo"
            }
        ];
        return (
            <Modal
                show={isModalShow || isModalEditShow}
                onClose={onCloseModal}
                title={isModalShow ? "New Design" : "Edit Design"}
            >
                <Form onSubmit={this.handleFormSubmit} className={styles.settings_modal_form}>
                    <p>Title *</p>
                    <Input
                        type="text"
                        name="title"
                        id="title"
                        value={title || ""}
                        onChange={this.handleInputChange}
                        className={errorClass(formErrors.title)}
                        errorMsg={formErrors.title}
                        placeholder="Your design title"
                    />
                    <p>Category *</p>
                    <Select
                        options={categories}
                        value={category || ""}
                        name="category"
                        onChange={this.handleInputChange}
                    />
                    <p>Description</p>
                    <Editor
                        editorState={form.desc}
                        wrapperClassName={styles.settings_modal_text_editor}
                        editorClassName={styles.settings_modal_text_editor_content}
                        onEditorStateChange={this.handleDescInputChange}
                        toolbar={{
                            options: ["inline", "link"]
                        }}
                    />
                    <p>Image URL *</p>
                    {this.renderImageUrlInput()}
                    <Button type="submit" classBtn="submit" disabled={!canSubmit}>
                        SUBMIT
                    </Button>
                </Form>
            </Modal>
        );
    }
}

export default SettingModal;
