import React, { Component, createRef } from "react";
import styles from "./ImageLoader.module.css";
import Placeholder from "../../assets/images/placeholder.png";
import BrokenPlaceholder from "../../assets/images/placeholder-broken.jpeg";

class ImageLoader extends Component {
    constructor(props) {
        super(props);
        this.imageRef = createRef();
        this.loader = null;
    }

    componentDidMount() {
        const { srcLoaded } = this.props;
        const tempImage = new Image();
        tempImage.src = srcLoaded;

        tempImage.onload = () => {
            if (this.imageRef.current) {
                this.imageRef.current.setAttribute("src", srcLoaded);

                this.imageRef.current.setAttribute("style", "display:block");

                this.imageRef.current.classList.add(styles.image_fade_in);
                this.loader.classList.add(styles.spinner_hidden);
            }
        };

        tempImage.onerror = () => {
            if (this.imageRef) {
                this.imageRef.current.setAttribute("src", BrokenPlaceholder);

                this.imageRef.current.setAttribute("style", "display:block");

                this.imageRef.current.classList.add(styles.image_fade_in);
                this.loader.classList.add(styles.spinner_hidden);
            }
        };
    }

    render() {
        const { className } = this.props;
        return (
            <div className={[styles.image_container, className].join(" ")}>
                <img
                    className={styles.image_loaded}
                    ref={this.imageRef}
                    alt="displayed img"
                    src={Placeholder}
                />
                <div
                    className={styles.spinner}
                    ref={spinnerLoadedElem => {
                        this.loader = spinnerLoadedElem;
                    }}
                >
                    <div className={styles.rect1} />
                    <div className={styles.rect2} />
                    <div className={styles.rect3} />
                    <div className={styles.rect4} />
                    <div className={styles.rect5} />
                </div>
            </div>
        );
    }
}

export default ImageLoader;
