import React, { Component, Fragment } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import "react-table/react-table.css";
import { CancelToken } from "axios";
import SettingModal from "./SettingModal";
import styles from "./Settings.module.css";
import { get } from "../../api";

import Button from "../../components/Common/Button";
import Table from "../../components/Common/Table";
import Alert from "../../components/Alert";

class Settings extends Component {
    state = {
        homeBackground: 2,
        showModal: false,
        showModalEdit: false
    };

    componentDidMount() {
        this.source = CancelToken.source();
        let designs;
        get(this.source).then(data => {
            data = designs;
        });
        designs
            && this.setState({
                designs: designs.data
            });
    }

    handleAddDesignClick = () => {
        const { showModal } = this.state;
        this.setState({
            showModal: !showModal,
            showModalEdit: false
        });
    };

    handleEditDesignClick = () => {
        const { showModalEdit } = this.state;
        this.setState({
            showModal: false,
            showModalEdit: !showModalEdit
        });
    };

    handleChangeBackground = id => {
        this.setState({
            homeBackground: id
        });

        Alert("success", "Background has been changed!");
    };

    handleCloseModal = () => this.setState({ showModal: false, showModalEdit: false });

    render() {
        const { designs, homeBackground, showModal, showModalEdit } = this.state;
        return (
            <Fragment>
                <SettingModal
                    isModalShow={showModal}
                    isModalEditShow={showModalEdit}
                    onCloseModal={this.handleCloseModal}
                />
                <div className={styles.settings_container}>
                    <div className={styles.settings_new_design_btn_contaiener}>
                        <Button
                            type="button"
                            classBtn="success"
                            onClick={this.handleAddDesignClick}
                        >
                            New
                            <FontAwesomeIcon icon="plus" size="sm" />
                        </Button>
                    </div>
                    <div className={styles.settings_design_list}>
                        <Table
                            data={designs}
                            onChangeBackground={this.handleChangeBackground}
                            onEditDesignClick={this.handleEditDesignClick}
                            idBackground={homeBackground}
                            type="settings"
                        />
                    </div>
                </div>
            </Fragment>
        );
    }
}

export default Settings;
